<template>
    <div class="recharge">
        <div class="content">
            <div class="top">
                <p>当前余额 ￥ <span class="num">100.00</span></p>
            </div>
            <div class="list">
                <div class="title">请选择充值金额</div>
                <ul class="items">
                    <li class="item">￥10</li>
                    <li class="item">￥50</li>
                    <li class="item">￥100</li>
                    <li class="item">￥500</li>
                    <li class="item">￥1000</li>
                    <li class="item">￥2000</li>
                </ul>
            </div>
            <div class="input">
                <span class="tip">￥</span>
                <input class="num_input" @blur="_blur" placeholder="请输入充值金额" type='number' pattern="[0-9]*" autofocus="autofocus"/>
            </div>
        </div>
        <div class="btn-wrap">
            <div class="btn">确定</div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {};
    },
    methods: {
        _blur() {
            setTimeout(function () {
                let scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
                window.scrollTo(0, Math.max(scrollHeight - 1, 0));
            }, 100);  
        },
    }
}
</script>

<style lang="stylus" scoped>
.recharge {
    width: 100%;
    font-family:HiraginoSansGB-W3;
    .content {
        background-color: #ffffff;
        padding: 0 30px;
        .top {
            height: 165px;
            line-height: 165px;
            text-align: center;
            font-size: 28px;
            color: #333333;
            border-bottom: 0.5px solid #F2F2F2;
            .num {
            font-size: 32px;  
            font-weight: bold;   
            }
        }
        .list {
            padding-top: 50px;
            border-bottom: 0.5px solid #F2F2F2;
            .title {
                font-size: 28px;
                font-weight: bold;
                color: #333333;
                margin-bottom: 30px;
            }
            .items {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                .item {
                    width: 200px;
                    height: 104px;
                    border:1px solid #FF8C45;
                    border-radius:8px;
                    box-sizing: border-box;
                    line-height: 104px;
                    margin-bottom: 40px;
                    font-size: 28px;
                    font-weight: bold;
                    color: #FF8C45;
                    padding-left: 33px;
                }  
            } 
        }
        .input {
            padding: 33px 0;
            font-size: 32px;
            color: #999999;
            .tip {
                color: #333333;
                font-weight: bold;
            }
        }
    }
    .btn-wrap {
        .btn {
            margin: 61px auto 0;
            width: 600px;
            height: 98px;
            line-height: 98px;
            background: #4A4C59;
            border-radius:10px;
            color: #ffffff;
            text-align: center;
        }
    }
}
</style>




